.search-input {
    height: 56px;
    width: 82%;
    position: relative;
}

.icon-filter {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 8%;
}

.icon-filter:hover {
    cursor: pointer;
}